<template>
	<div style="padding:17px;text-align: center;">
		<div class="top1">
			<i class="fa fa-user-circle" style="font-size:70px;margin: 30px 30px 15px 30px;"></i>
			<!-- 根据name是否为空显示隐藏 -->
			<p style="padding-bottom: 20px;color: #e0f47a;" @click="login" v-if="name==null">{{name}}我有账号，点击登录</p>
			<p style="padding-bottom: 20px;" v-if="name!=null">{{name}}</p>
		</div>

		<div class="col-sm-12 col-md-6">
			<div class="box companyinfo-box myexam" @click="ks">
				<i class="fa fa-map-o"></i>
				<div class="textfromtop">我的考试</div>
			</div>
			<div class="box companyinfo-box myexam" @click="lx">
				<i class="fa fa-file-text-o"></i>
				<div class="textfromtop">我的练习</div>
			</div>
			<div class="box companyinfo-box myexam" @click="kc">
				<i class="fa fa-leanpub"></i>
				<div class="textfromtop">我的课程</div>
			</div>
		</div>
		<div class="col-sm-12 col-md-6">
			<div class="box version-box ctb" @click="cj">
				<i class="fa fa-line-chart"></i>
				<div class="textfromtop">考试成绩</div>
			</div>
			<div class="box version-box ctb" @click="ctb">
				<i class="fa fa-calendar-times-o"></i>
				<div class="textfromtop">错题本</div>
			</div>
			<div class="box version-box ctb" @click="jf">
				<i class="fa fa-star"></i>
				<div class="textfromtop">我的积分</div>
			</div>
		</div>
		<div class="top3" v-if="name!=null">
			<div style="background-color:#99A8CA" @click="xgmm">
				<i class="fa fa-key"></i>
				修改密码
			</div>
			<div style="background-color:#B9BECB" @click="login">
				<i class="fa fa-sign-out"></i>
				退出登录
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {

			};
		},
		methods: {
			//跳转我的考试页面并控制底部导航变色
			ks() {
				this.$router.push("/ks")
				this.$store.commit("xz", 2)
			},
			//跳转我的练习页面并控制底部导航变色
			lx() {
				this.$router.push("/lx")
				this.$store.commit("xz", 3)
			},
			//跳转我的课程页面并控制底部导航变色
			kc() {
				this.$router.push("/kc")
				this.$store.commit("xz", 4)
			},
			//跳转考试成绩页面并控制底部导航变色
			cj() {
				this.$router.push("/cj")
				this.$store.commit("xz", 2)
			},
			//跳转错题本页面
			ctb() {
				this.$router.push("/ctb")
			},
			//跳转我的积分页面
			jf() {
				this.$router.push("/jf")
			},
			//跳转修改密码页面
			xgmm() {
				this.$router.push("/xgmm")
			},
			//跳转登录页面，如果已登录则清空登录信息
			login() {
				if(this.$store.state.name!=null){
					this.$store.state.name=null
				}
				this.$router.push("/login")
			}
		},
		computed: {
			//取出vuex里的name变量
			name() {
				return this.$store.state.name
			}
		},
		//页面加载控制台打印用户信息
		created() {
			console.log(this.$store.state.name)
		}
	};
</script>
<style scoped>
	.top1 {
		background-color: #4c6aa9;
		border-radius: 20px;
		color: white;
	}

	.top3 div {
		margin-top: 27px;
		display: inline-block;
		width: 50%;
		height: 150px;
		line-height: 150px;
		font-size: 40px;
		color: white;
	}

	.col-md-6 {
		float: left;
		width: 50%;
	}

	.box {
		text-align: center;
		color: #fff;
	}

	.myexam {
		background: #6785C3;
		box-shadow: 13px 13px 13px #d8d8d8;
		border-radius: 18px;
	}

	.companyinfo-box {
		height: 226px;
		margin-top: 18px;
		margin-right: 20px;
	}

	.col-md-6 i {
		font-size: 2.6rem;
		margin-top: 28px;
	}

	.textfromtop {
		margin-top: 32px;
	}

	.ctb {
		background: #8b8b8B;
		box-shadow: 13px 13px 14px #d8d8d8;
		border-radius: 18px;
	}

	.version-box {
		height: 226px;
		margin-bottom: 17px;
		margin-top: 18px;
		margin-left: 20px;
	}
</style>
